.element {
    @apply flex flex-col m-3;


    input {
        @apply pl-3 rounded-lg border border-black border-opacity-60 mb-3;
    }

    select {
        @apply pl-3 rounded-lg border border-black border-opacity-60 mb-3;
    }

    button {
        transition-delay: 15ms;

        @apply bg-green-300 px-2 py-1 mx-1 rounded-lg border border-black border-opacity-30 transition-colors;
        
        &:hover {
            @apply bg-green-400;
        }
        &:active {
            transition-delay: 5ms;
            @apply bg-orange-400;
        }
    }
}